import React, { useContext, useEffect, useState, useRef } from 'react';
import { Button, Collapse, CustomInput, Nav, Navbar, Tooltip } from 'reactstrap';
import Scrollbar from 'react-scrollbars-custom';

import { navbarBreakPoint } from '../../config';
import Logo from './Logo';
import routes from '../../routes';
import NavbarVerticalMenu from './NavbarVerticalMenu';
import ToggleButton from './ToggleButton';
import AppContext from '../../context/Context';

// const isBurgerMenuShown = () => {
//   const burgerMenu = document.getElementById('burgerMenu');
//   return !(getComputedStyle(burgerMenu).display === 'none');
// };

const NavbarVertical = () => {
  const [tooltipOpen, setTooltipOpen] = useState(false);

  const navBar = useRef(null);

  const {
    isFluid,
    setIsFluid,
    isRTL,
    setIsRTL,
    isDark,
    setIsDark,
    showBurgerMenu,
    isNavbarVerticalCollapsed,
    setIsNavbarVerticalCollapsed
  } = useContext(AppContext);
  //controll componentdid mount and unmount of hover effect
  useEffect(() => {
    if (!isNavbarVerticalCollapsed) {
      document.getElementsByTagName('html')[0].classList.add('navbar-vertical-collapsed');
    }
    return () => {
      document.getElementsByTagName('html')[0].classList.remove('navbar-vertical-collapsed-hover');
    };
  }, [isNavbarVerticalCollapsed]);

  //controll mouseEnter event
  let time = null;
  const handleMouseEnter = () => {
    if (document.getElementsByTagName('html')[0].classList.contains('navbar-vertical-collapsed')) {
      time = setTimeout(() => {
        document.getElementsByTagName('html')[0].classList.add('navbar-vertical-collapsed-hover');
      }, 100);
    }
  };
  //controll tooltipOpen
  const toggle = () => setTooltipOpen(!tooltipOpen);

  return (
    <Navbar expand={navbarBreakPoint} className="navbar-vertical navbar-glass " light>
      <Tooltip placement="left" isOpen={tooltipOpen} target="TooltipExample" toggle={toggle} innerClassName="p-2">
        Toggle Navigation
      </Tooltip>
      <div className="d-flex align-items-center">
        <ToggleButton
          isNavbarVerticalCollapsed={isNavbarVerticalCollapsed}
          setIsNavbarVerticalCollapsed={setIsNavbarVerticalCollapsed}
        />
        <Logo at="navbar-vertical" width={40} />
      </div>

      <Collapse
        navbar
        isOpen={showBurgerMenu}
        className="bg-200"
        innerRef={navBar}
        onMouseEnter={() => {
          handleMouseEnter();
        }}
        onMouseLeave={() => {
          clearTimeout(time);
          document.getElementsByTagName('html')[0].classList.remove('navbar-vertical-collapsed-hover');
        }}
      >
        <Scrollbar
          style={{ height: '87vh', display: 'block' }}
          rtl={isRTL && true}
          trackYProps={{
            renderer: props => {
              const { elementRef, ...restProps } = props;
              return <span {...restProps} ref={elementRef} className="TrackY" />;
            }
          }}
        >
          <Nav navbar vertical>
            <NavbarVerticalMenu routes={routes} />
          </Nav>
          <div className={`settings px-3 px-${navbarBreakPoint}-0`}>
            <hr className="border-300 my-3" />
            <h6 className="text-uppercase fs--2 font-weight-semi-bold ls text-600">Settings</h6>
            <div
              className="bg-light border py-card rounded"
              style={{ paddingLeft: '1.4375rem', paddingRight: '1.4375rem' }}
            >
              <CustomInput
                type="checkbox"
                id="dark"
                name="dark"
                label="Dark Mode"
                checked={isDark}
                onChange={({ target }) => setIsDark(target.checked)}
              />
              <CustomInput
                type="checkbox"
                id="rtl"
                name="rtl"
                label="RTL Layout"
                checked={isRTL}
                onChange={({ target }) => setIsRTL(target.checked)}
              />
              <CustomInput
                type="checkbox"
                id="fluid"
                name="fluid"
                label="Fluid Container"
                checked={isFluid}
                onChange={({ target }) => setIsFluid(target.checked)}
              />
            </div>
            <Button
              tag={'a'}
              href="https://themes.getbootstrap.com/product/falcon-admin-dashboard-webapp-template-react/"
              target="_blank"
              color="primary"
              size="sm"
              block
              className="my-3 btn-purchase"
            >
              Purchase
            </Button>
          </div>
        </Scrollbar>
      </Collapse>
    </Navbar>
  );
};

export default NavbarVertical;
